
.agent-content{width:100%;position:absolute;top:1.3rem;left:0;margin-bottom:1.3rem;overflow-y:auto
}
.agent-content .now-agent-group{height:1rem;line-height:1rem;padding-left:0.53rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;text-align:left
}
.agent-content .now-agent-group .now-agent-head{color:#0099FF
}
.agent-content .now-agent-group .now-agent-text{margin-left:0.53rem
}
.agent-set-btn{width:100%;position:fixed;left:0;bottom:0;display:-webkit-box;-webkit-box-align:center;-webkit-box-pack:justify
}
.reset-btn{width:50%;height:1.3rem;line-height:1.3rem;color:#FFF;background-color:#BCBCBC;text-align:center;font-size:0.34rem
}
.reset-btn:active{background-color:#A7A7A7
}
.yes-btn{width:50%;height:1.3rem;line-height:1.3rem;color:#FFF;background-color:#FC9153;text-align:center;font-size:0.34rem
}
.yes-btn:active{background-color:#dc8553
}



