
.editModel{width:100%;height:100%;background:#FFF;position:fixed;z-index:100;left:0;top:0
}
.editModel .edit-form{width:100%;height:100%;margin-top:50px;padding-bottom:2.3rem;text-align:left;font-size:0.39rem;overflow:scroll
}
.editModel .edit-form .os-radio{width:250px
}
.editModel .edit-form .os-radio .os-radio-item{margin-right:0.26rem
}
.editModel .edit-form .cov-datepicker{width:250px !important
}
.editModel .edit-form .mint-cell{text-decoration:none !important
}
.date-warp{height:30px;line-height:30px
}
.date-warp .datepicker-overlay{-webkit-animation:fadein 0.5s
}
.date-warp .cov-date-body{-webkit-transform:translate(-50%, -50%)
}
.date-warp .cov-date-next:before,.date-warp .cov-date-previous:before{-webkit-transform:rotate(45deg)
}
.date-warp .cov-date-previous:before{-webkit-transform:rotate(-45deg)
}
.date-warp .cov-date-previous::after{-webkit-transform:rotate(45deg)
}
.date-warp .cov-date-next::after,.cov-date-previous::after{-webkit-transform:rotate(-45deg)
}
.date-warp .cov-date-body{background-color:#716CA8 !important
}
.date-warp .cov-date-caption{color:#fff !important
}
.date-warp .checked{background-color:#FC9153 !important
}
.media-control{border:0;width:250px
}



