
.fastGroup{width:100%;height:1.06rem;line-height:1.06rem;position:relative
}
.fastGroup .fastSelecter{width:7rem;height:0.8rem;line-height:0.8rem;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);display:-webkit-box;border:1px solid #CAAD9D
}
.fastGroup .fastSelecter .fast-item{-webkit-box-flex:1;background:#FFFFFF;border-left-color:#CAAD9D;font-size:0.4rem;font-family:'微软雅黑'
}
.fastGroup .fastSelecter .fast-item.now{color:#FFF;background-color:#FC9153
}
.tran-group{position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);width:100%;height:0.8rem;font-family:"iconfont" !important
}
.tran-group .down:before{content:'\e60c';color:#2ACBCB;font-size:0.6rem
}
.tran-group .up:before{content:'\e76e';color:#2ACBCB;font-size:0.6rem
}
.component-fade-enter-active,.component-fade-leave-active{transition:opacity .3s ease
}
.component-fade-enter,.component-fade-leave-active{opacity:0
}



