
.tip{width:90%;background-color:#FFF;border:1px solid #E2E2E2;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);font-family:'微软雅黑'
}
.tip .tip-header{text-align:center;height:1rem;line-height:1rem;font-size:0.46rem;border-bottom:1px solid #E2E2E2;background-color:#ffffcc;font-weight:bold
}
.tip .tip-body{height:2rem;line-height:2rem;font-size:0.4rem;border-bottom:1px solid #E2E2E2
}
.tip .tip-footer{height:1rem;line-height:1rem;font-size:0;text-align:center
}
.tip .tip-footer .refuse{display:inline-block;width:50%;font-size:0.4rem;background-color:#EF4F4F;color:#FFF
}
.tip .tip-footer .refuse:active{background-color:#E64340
}
.tip .tip-footer .accet{display:inline-block;width:50%;font-size:0.4rem;background-color:#04BE02;color:#FFF
}
.tip .tip-footer .accet:active{background-color:#1AAD19
}



