
.checkRow{margin:5px 0 0 0;display:-webkit-box;-webkit-box-align:center;-webkit-box-pack:justify
}
.checkRow .date-warp{height:30px;line-height:30px
}
.checkRow .date-warp .datepicker-overlay{-webkit-animation:fadein 0.5s
}
.checkRow .date-warp .cov-date-body{-webkit-transform:translate(-50%, -50%)
}
.checkRow .date-warp .cov-date-next:before,.checkRow .date-warp .cov-date-previous:before{-webkit-transform:rotate(45deg)
}
.checkRow .date-warp .cov-date-previous:before{-webkit-transform:rotate(-45deg)
}
.checkRow .date-warp .cov-date-previous::after{-webkit-transform:rotate(45deg)
}
.checkRow .date-warp .cov-date-next::after,.checkRow .cov-date-previous::after{-webkit-transform:rotate(-45deg)
}
.checkRow .date-warp .cov-date-body{background-color:#716CA8 !important
}
.checkRow .date-warp .cov-date-caption{color:#fff !important
}
.checkRow .date-warp .checked{background-color:#FC9153 !important
}
.checkRow .right-area{height:30px;line-height:30px;font-size:13px;font-family:'微软雅黑'
}
.checkRow .right-area.hide{visibility:hidden
}
.checkRow .left-area{height:30px;line-height:30px;font-size:13px;font-family:'微软雅黑'
}



