
.chartModel{width:100%;height:100%;background:#FFF;position:fixed;z-index:100;left:0;top:0
}
.chartModel .chart-content{width:100%;height:auto;margin-top:50px
}
@media screen and (orientation: portrait){
.chartModel .chart-content #keyIndexChart{width:100%;height:14rem
}
}
@media screen and (orientation: landscape){
.chartModel .chart-content #keyIndexChart{width:100%;height:6rem
}
}



